@import url('https://fonts.googleapis.com/css2?family=Koulen&family=Noto+Sans+TC&family=Roboto+Mono&family=Rubik&display=swap');

*{
    box-sizing: border-box;
}
body{
    font-family:'Rubik' ,san-serif;
    background-color: #f5f5f5;
    margin: 0 auto;

}
.header {
  height: 20px;
  background-color: #dfb9e1;
}

.footer {
    background-color: #dfb9e1;

}
        h1{
            color:rgb(167, 130, 205);
            text-align: center;
            font-size: 10rem;
            opacity:0.4;
        }
        form{
            margin: 0 auto;
            max-width: 100%;
            width: 500px;
            box-shadow: 0 4px 10px rgba(0,0,0,0.1);
        }
        .input{
            color: #456;
            font-size: 1.5rem;
            padding: 1rem 2rem;
            width: 100%;
            display: block;
            border:none;
        }
        .input::placeholder{
            color:#b5b5b5;
        }

        .todos{
            background-color: #fff;
            padding: 0;
            margin: 0;
            list-style-type: none;
            li{
                color: #756;
                border-top: 1px solid rgb(191, 190, 190);
                font-size: 24px;
                padding: 1rem 2rem;
                cursor: pointer;
            }
        }
        small{
            margin: 0 auto;
            color:rgb(195, 195, 195);
        }
        .tips{
            display: flex;
            justify-content: space-between;
        }
        .completed{
            color:rgb(191, 190, 190);
            text-decoration: line-through;
        }